.info{
    display: flex;
    flex-direction: column;
    align-items: center;


    padding: 25px 0 75px 0;


    h2{
        margin: 0px 0 38px 0;
        margin-bottom: 0;
        font-size: 40px;
        line-height: 1.05;
        font-weight: 500;
        letter-spacing: .008em;
    }

    h3{
        width: 1200px;
        font-size: 28px;
        line-height: 1.09375;
        font-weight: 500;
        letter-spacing: .011em;
        text-transform: capitalize;
        margin-top: 25px;
        color: #646464;
    }

    &__chart{
        width: 1200px !important;
        height: 400px !important;
    }

    &__img{
        width: 1200px;
        height: 200px;


        margin-top: 15px;

        background: #000;
    }
}