.wave{
    animation: pulse 2s infinite;

    @keyframes pulse {
        0% {
            stroke-width: 3px;
        }
    
        70% {
            stroke-width: 6px;
        }
    
        100% {
            stroke-width: 3px;
        }
    }
    
}

.model{
    border-bottom: 1px solid #dedede;

    display: flex;
    flex-direction: column;
    align-items: center;

    h2{
        width: 1200px;
        margin: 48px 0 38px 0;
        font-weight: 600;
        letter-spacing: -2px;
        color: #000;
        text-transform: capitalize;

        span{
            background: -webkit-linear-gradient(
                -70deg
                , #f25050, #0a66d1);
            background-clip: text;
            color: transparent;
        }
    }
}

.model-inner-wrapper{
    display: flex; 
    flex-direction: column;
}

.model-scheme{
    width: 1000px;
    height: 400px;

    border: 1px solid #d9d9d9;
    padding: 7px;

    display: flex;

    &__waves{
        width: 500px;
        margin-left: -46px;

        svg{
            width: 100%;
            height: 100%;
        }
    }

    &__screen{
        display: flex;

        cursor: pointer;

        &__wall{
            transition: all 0.3s ease;

            width: 15px;
            background-color: #333;
        }

        &__name{
            transition: all 0.3s ease;

            writing-mode: vertical-rl;
            font-size: 43px;
            text-transform: uppercase;
            margin-left: -5px;
        }

        &:hover{
            .model-scheme__screen__wall{
                background-color: #777;
            }

            .model-scheme__screen__name{
                margin-top: -7px;
            }
        }
    }

    &__light{
        cursor: pointer;

        width: 100px;
        height: 100%;

        transition: all 0.3s ease;

        svg{
            width: 100%;
            height: 100%;
        }

        &:hover{
            margin-left: -5px;
            margin-right: 5px;
        }
    }

    &__slit{
        z-index: 100;
        cursor: pointer;

        display: flex;
        flex-direction: row;
        justify-content: center;

        &:hover{
            .model-scheme__slit__wall{
                background-color: #777;
            }

            .model-scheme__slit__name{
                margin-top: -7px;
            }
        }

        &__name{
            transition: all 0.3s ease;

            
            writing-mode: vertical-rl;
            font-size: 43px;
            text-transform: uppercase;
        }

        &__wall{
            transition: all 0.3s ease;

            width: 15px;
            background-color: #333;
            
            display: flex;
            align-items: center;

            &__slit{
                width: 15px;
                height: 30px;
                background-color: #fff;
            }
        }
    }
}

.model-params{
    width: 1000px;

    padding: 20px 0 10px 0;

    &-item-wrapper{
        display: flex;
        justify-content: center;
    }

    &-input{
        margin: 10px 0;

        transition: all 0.2s ease;

        &.disabled{
            opacity: 0.5;

            input{
                pointer-events: none;
            }
        }

        &-inner-wrapper{
            display: flex;
            justify-content: center;
        }

        &-units{
            text-align: center;
        }

        input{
            width: 400px;

            margin: 0 8px;
        }

        &-value{
            font-size: 19px;
            line-height: 1.5294717647;
            font-weight: 400;
            letter-spacing: -0.021em;
            width: 200px;
            color: #000000;

            &:nth-child(1){
                text-align: right;
            }

            &:nth-child(2){
                text-align: left;
            }
        }
    }
    
    &-item{
        width: 100px;
        height: 100px;
        border-width: 1px;
        border-style: solid;
        border-radius: 12px;
        border-color: #d2d2d7;
        position: relative;

        margin: 0 5px;

        cursor: pointer;

        transition: all 0.2s ease-in-out;

        &.selected{
            background-color: #e7e7e7;
            border-color: #8e8e8e;
        }

        &__value{
            margin-left: -2.45px;
            width: 100px;
            height: 70px;
            line-height: 98px;
            font-size: 34px;
            letter-spacing: -4.9px;
            text-align: center;
            position: absolute;
        }

        &__units{
            width: 100px;
            height: 25px;
            bottom: 0;
            text-align: center;
            position: absolute;
        }

        &__name{
            position: absolute;
            top: 1px;
            right: 5px;

            font-size: 29px;
            text-align: center;
            //line-height: 100px;
            color: #c3c3c3;
        }
    }

}